<template>
  <div id="search">

    <!-- 我的报 Warp -->
    <div id="search_Warp" >
      <div class="searchModel">
        <div class="back" @click="handleBack"><img src="../../images/main/nav_back_1.png"></div>
        <div class="input">
          <img src="../../images/myinfo/search_not.png" width="18" height="18">
          <input placeholder="在这里可以找到答案...." v-model="searchText">
        </div>
        <span class="searchBtn" @click="searchActin()">搜 索</span>
      </div>
      <!-- ****** 全部 ****** -->
      <div id="warp_0">
        <li v-for="item in list">
          <router-link :to="{path: '/askDetail', query:{qid: item.q_id}}" v-if="type==1">
            <AskList  :itemData="item"></AskList>
          </router-link>

          <router-link :to="{path: '/baikeDetail', query:{id: item.ask_baike_id}}" v-if="type==2">
            <BaikeCell :itemData="item"></BaikeCell>
          </router-link>
       </li>
      </div>
    </div>

    <!-- <div v-if="!reportList.length" class="more">暂无数据</div> -->

  </div>
</template>

<script>
import { Toast,Indicator,Loadmore} from 'mint-ui'
import {getStore, setStore} from '../../components/common/mUtils'
import AskList from '../../page/home/children/AskList'
import BaikeCell from '../../page/home/children/baikeCell'
export default {
  data(){
    return{
      userID:'',
      userToken:'',
      list:[],   // 全部
      searchText:'',
      type:1,
    }
  },
  created(){

  },
  mounted(){
    this.userID = getStore('userID')
    this.userToken = getStore('userToken')
    this.type = this.$route.query.type
    let keyStr = this.$route.query.keyStr
    if (keyStr) {
      this.searchText = decodeURI(decodeURI(this.$route.query.keyStr))
    }

    if (this.searchText != '') {
      this.getData()
    }
    //
  },
  filters:{
  },
  components:{
    AskList,
    BaikeCell,
  },
  methods: {
    handleBack(){
      this.$router.go(-1);
    },
    gotoAddress(path){
      this.$router.push(path)
    },
    searchActin(){
      this.lList = [];
      if (this.searchText != '') {
        this.getData()
      }
    },
    // 获取数据
    getData(){
      Indicator.open();
      let pStr = '?s='+this.searchText

      let methods = this.type == 1 ? 'list' : 'baike'

      let getUrl = this.CFWConfig.APIHost + '/v3/ask/'+methods+pStr
      this.$http.get(getUrl).then(response => {
        let code = response.data.code
        if (code == 200) {
          this.list = response.data.result
          // let arr = response.data.result
          // if (arr.length > 0) {
          //   // 如果小于10条就是加载完了
          //   this.complete = arr.length < 10 ? true :false
          //   arr.forEach((item) => this.list.push(item))
          // } else {
          //   this.complete = true
          //   this.allLoaded = true
          // }
        }
        // this.searchKey.page++
        // this.loading = false // 请求完成
        Indicator.close();
      }, response => {
        Toast('请求失败');
        console.log(response);
        Indicator.close();
      });
    },
    showMsg(msg){
      Toast({
        message:msg,
        duration: 800
      });
    },
  },

}
</script>

<style>
/*引入公共样式*/
@import '../../style/common/style.css';

#search{
  background-color: #f5f5f5;
}


.searchModel{
  height: 40px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding-left: 10px;
  background-color: rgb(245,245,245);

}

.searchModel .back{
  display: flex;
  align-items: center;
  flex-direction: row;
}

.searchModel .back img{
  width: 20px;
  height: 22px;
  margin-right: 8px;
}

.searchModel .input{
  /*margin: 5px;*/
  height: 70%;
  background-color: #fff;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 5px;
  flex-grow: 1;
}

.searchModel .input input{
  background-color: #fff;
  color: #000;
  margin-left: 2px;
  padding-left: 10px;
  font-size: 14px;
  width: 100%;
}

.searchModel .searchBtn{
  width: 60px;
  height: 100%;
  font-size: 15px;
  line-height: 45px;
  /*border: 1px solid red;*/
  font-weight: bold;
  color: #8a8a8a;
}

.more{
  padding-top: 20px;
  background-color: #fff;
}

#warp_0 .baikeCell{
  background-color: #fff;
}

#warp_0 .baikeCell  p{
  padding: 0px 10px;
}

#warp_0 .baikeCell .CellLabel{
  margin-left: 10px;
}

</style>



