<template>
  <div class="ask_list">
      <span class="user">
        <img :src="getUserAvatar" />
        <i>{{itemData.trueName}}</i>
      </span>
      <div class="images" v-if="itemData.imgs.length"><img :src="getImgs+'_ask'" /></div>
      <h3>{{itemData.q_title}}</h3>
      <p>{{itemData.q_content}}</p>
      <div class="bottom">
        <span>{{itemData.q_hits}}已围观</span>
        <span>{{itemData.q_replys}}回答</span>
      </div>
  </div>
</template>

<script>
import {getStore,imgBaseUrl} from '../../../components/common/mUtils'
export default {
  data(){
    return{
      imgBaseUrl,
      data:[],
    }
  },
  props:['itemData'],
  filters:{
    parseFloat(a){
      return parseFloat(a)
    }
  },
  computed:{
    getUserAvatar:function(){
      if(this.itemData){
        if (this.itemData.avatarPath == '' || !this.itemData.avatarPath ) {
          return "http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y"
        }else {
          let indexOf = this.itemData.avatarPath.indexOf('http://tu.07358.com')
          if(indexOf != -1){
            return this.itemData.avatarPath
          }else {
            if (this.itemData.avatarPath == '          ') {
              return "http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y"
            }else {
              return imgBaseUrl + this.itemData.avatarPath
            }
          }
        }
      }else {
        return "http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y"
      }
    },
    getImgs(){
      let imags = this.itemData.imgs.split(',')
      return imags[0];
    }
  },
  created(){
  },
  mounted(){
  },
  components:{
  },
  methods: {

  }

}

</script>

<style scoped>
/***** 问题列表 *****/
.ask_list{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 0 10px;
  border-bottom: 20px solid #f6f6f6;
  /*border-top: 1px solid #eee;*/
  background-color: #fff;
}
/*.ask_list li{border-bottom: 1px solid #eee;}*/
.ask_list .images{}
.ask_list .images img{
  width: 100%;
  height: 160px;
}

.ask_list .user{
  height: 35px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.ask_list .user img{
  width: 20px;
  height: 20px;
  border-radius: 10px;
}

.ask_list .user i{
  font-size: 13px;
  line-height: 30px;
  margin-left: 5px;
  font-weight: bold;
  font-style: normal;
}

.ask_list h3{
  font-size: 16px;
  color: #000;
  font-weight: 400;
  height: 36px;
  line-height: 36px;
  text-align: left;
  /*margin-top: 10px;*/
  font-weight: bold;
  overflow:hidden;
  text-overflow:ellipsis;
  display:-webkit-box;
  -webkit-box-orient:vertical;
  -webkit-line-clamp:1;
}

.ask_list p{
  text-align: left;
  font-size: 14px;
  line-height: 20px;
  /*background-color: #f7f7f7;*/
  padding:  3px 0px 0px;
  overflow:hidden;
  text-overflow:ellipsis;
  display:-webkit-box;
  -webkit-box-orient:vertical;
  -webkit-line-clamp:3;
  color: rgb(50,50,50);
}

.ask_list .bottom{
  height: 40px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  font-size: 13px;
  color: #888787;
}

.ask_list .bottom span{
  margin-right: 10px;
}

.ask_list .bottom .R{
  display: flex;
  align-items: center;
  margin-right: 10px;
  line-height: normal;
  margin-right: 10px;
}
.ask_list .bottom .R img{
  width: 18px;
  height: 18px;
  margin-right: 5px;
}


</style>



