<template>
  <div class="CellLabel">
    <p v-for="item in getLabel">{{item}}</p>
  </div>
</template>

<script>
export default {
  data(){
    return{

    }
  },
  props:['label'],
  filters:{
    parseFloat(a){
      return parseFloat(a)
    }
  },
  computed:{
    getLabel(){
      let labels = this.label.split(' ')
      let newArr = []
      labels.forEach((item) => {
        if (item != '') {
          newArr.push(item)
        }
      })
      return newArr;
    }
  },
  created(){
  },
  mounted(){
  },
  components:{
  },
  methods: {

  }

}

</script>

<style scoped>
.CellLabel{
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  font-size: 13px;
  margin-top: 10px;
}

.CellLabel p{
  padding: 3px 6px;
  color: #849aae;
  background-color:#f4f7f9;
  margin-right: 10px;
}

</style>



